import { Component } from "react";
import { toast } from "react-toastify";
import ImageGalleryItem from "../ImageGalleryItem/ImageGalleryItem";
import API from '../API'
import s from './ImageGallery.module.css';
import Loader from '../Loader'
import Button from "../Button";




export default class ImageGallery extends Component {
    state={
        query:null,
        loading:false,
        error:false,
        dataSet:[],
        page:1

    }
    componentDidUpdate(prexProps,prevState){
        const prevQuery=prexProps.galleryName;
        const nextQuery=this.props.galleryName;
        
        if(prevQuery!==nextQuery){
            this.setState({loading:true,dataSet:[]})
            API.fetchAPI(nextQuery)
            .then(data=>{
                if(data.total===0){ 
                    toast.error('Ничего не найденно по вашему запросу.Введите коректный запрос')
                }
                this.setState({dataSet:data})})
            .catch(error=>this.setState({error}))    
            .finally(()=>this.setState({loading:false}))
        }
    }
    handelButton(e){
        
    }
    render(){
        return(
        <div>
            {this.state.error&&<div>Произошла ошибка при запросе</div>}
            {this.state.loading&&<Loader/>}
            {!this.props.galleryName &&<div>Стейт пустой, введите что хотите найти</div>}
            {this.state.dataSet.hits&&
            <>
             <ul className={s.ImageGallery}>{this.state.dataSet.hits.map(card=>
                 <ImageGalleryItem key={card.id}
                 imageSrc={card.webformatURL}
                 modalImage={card.openModalImage}  />)}</ul>
             <div><Button onClick={this.handelButton}/></div>
            </>}
        </div>

    )
        
    }
}
    
            
            

        
    
        
        
    
        
      
