import React from 'react';
import s from './ImageGalleryItem.module.css'



const ImageGalleryItem= ({key,imageSrc,modalImage})=>(
    <li className={s.ImageGalleryItem} key={key}>
<img src={imageSrc} alt={modalImage} className={s.ImageGalleryItem} />
  </li>

)

export default ImageGalleryItem;